html, body {
  --font-color: #00acdc;
  margin: 0;
  padding: 0;
  font-family: Roboto, sans-serif;
}

.flex-container {
  box-sizing: border-box;
  justify-content: space-between;
  align-items: center;
  width: 100%;
  margin: 0;
  padding: 0 50px;
  display: flex;
}

.logo {
  width: 40px;
  height: 40px;
}

.nav__list {
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
}

.nav__list li:not(:last-child) {
  margin-right: 20px;
}

.nav__link {
  text-align: center;
  color: #000;
  text-transform: uppercase;
  font-size: 12px;
  line-height: 60px;
  display: inline-block;
  position: relative;
}

.nav__link:hover, .nav__link.is-active {
  color: var(--font-color);
}

.nav__link.is-active:after {
  content: "";
  background-color: var(--font-color);
  border-radius: 8px;
  width: 100%;
  height: 4px;
  position: absolute;
  bottom: 0;
  left: 0;
}
